import { Link } from "react-router-dom";

function NavBar (props){
    return(

        <nav className="navbar navbar-expand-lg navbar-dark py-2 position-fixed top-0 end-0 start-0">
              <div className="container">
                <Link className="navbar-brand fw-bolder fs-3 " to='/home'>Noxe</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                  
                  <ul className="navbar-nav me-auto mb-2 mb-lg-0">

                    {props.userData?<><li className="nav-item">
                      <Link className="nav-link active fw-light" aria-current="page" to='/home'>Home</Link>
                    </li>

                    <li className="nav-item">
                      <Link className="nav-link active fw-light" aria-current="page" to='/movies'>Movies</Link>
                    </li>

                    <li className="nav-item">
                      <Link className="nav-link active fw-light" aria-current="page" to='/tv'>TvShow</Link>
                    </li>

                    <li className="nav-item">
                      <Link className="nav-link active fw-light" aria-current="page" to='/people'>People</Link>
                    </li></>:''}
                    
                  </ul>
                 
                  
                  <ul className="navbar-nav  mb-2 mb-lg-0 align-items-center">

                    {props.userData?<>
                      <li className="nav-item">
                        <Link className="nav-link active fw-light" aria-current="page" to='/home' ><i className="fa-brands fa-facebook"></i></Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link active fw-light" aria-current="page" to='/home' ><i className="fa-brands fa-spotify"></i></Link>
                      </li>

                      <li className="nav-item">
                        <Link className="nav-link active fw-light" aria-current="page" to='/home' ><i className="fa-brands fa-instagram"></i></Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link active fw-light" aria-current="page" to='/home' ><i className="fa-brands fa-youtube"></i></Link>
                      </li>


                      <li className="nav-item ">
                      <p className="nav-link active fw-light mb-lg-0" aria-current="page" onClick={props.logOut}>LogOut</p>
                      </li></>:
                        <>
                        

                        <li className="nav-item">
                          <Link className="nav-link active fw-light" aria-current="page" to='/register' >Register</Link>
                        </li>
                  
                        <li className="nav-item">
                          <Link className="nav-link active fw-light" aria-current="page" to='/login'>LogIn</Link>
                        </li></>}
            
                    </ul>




    </div>
  </div>
</nav>
    )
}

export default NavBar;