import axios from "axios";
import Joi from "joi";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";


function Register(){

    const[Users,setUsers]=useState({
        first_name:'',
        last_name:'',
        email:'',
        password:'',
        age:0
    });

    const [errors,setErrors]= useState('');

    const [loadingSpiner,setLoadingSpiner]=useState(false);

    let Navigate = useNavigate();

    const[errorList,setErrorList]=useState([]);

    function getUser(e){
            let myUsers={...Users};
            myUsers[e.target.name]=e.target.value;
            setUsers(myUsers);
        

    }


    function validation(){
        let schema = Joi.object({
            first_name:Joi.string().alphanum().min(3).max(10).required(),
            last_name:Joi.string().alphanum().min(3).max(10).required(),
            email:Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
            password:Joi.string().pattern(/^[a-z]{4,10}$/).message({'*':'password invalid should contain from 4 to 10 characters'}),
            age:Joi.number().min(16),
        })

        return schema.validate(Users,{abortEarly:false});
    }

    async function submitRegister(e){
        e.preventDefault();
        setLoadingSpiner(true);

        let validate = validation(Users);
        
        if(validate.error){

            setLoadingSpiner(false);
            setErrorList(validate.error.details);


        }

        else{
            let {data} = await axios.post('https://route-egypt-api.herokuapp.com/signup',Users)
            
    
            if(data.message ==='success'){
                setLoadingSpiner(false);
                Navigate('/login');
            }
            else{
                    let datamessage =data.message;
                    let index= datamessage.lastIndexOf(':')+1;
                    let final =datamessage.slice(index);
                // setErrors(data.message);
                setErrors(final);
                setLoadingSpiner(false);
                
            }
        }
    
        
    }

   
    


    return (

       <div className=" Register d-flex align-items-center justify-content-center py-5" >

           <div className="w-75 py-3">
           <h2 className=" fw-normal my-4">Registration Form:</h2>
            <form onSubmit={submitRegister} >
            
                <label htmlFor="first_name" className="fs-5 fw-light" >First Name:</label>
                <input onChange={getUser} type="text" id="first_name" name="first_name" className="form-control bg-transparent my-3 text-white" />

                <label htmlFor="last_name" className="fs-5 fw-light" >Last Name:</label>
                <input onChange={getUser} type="text" id="last_name" name="last_name" className="form-control bg-transparent my-3 text-white" />

                <label htmlFor="email" className="fs-5 fw-light" >Email:</label>
                <input onChange={getUser} type="email" id="email" name="email" className="form-control bg-transparent my-3 text-white" />


                <label htmlFor="password" className="fs-5 fw-light" >Password:</label>
                <input onChange={getUser} type="password" id="password" name="password" className="form-control bg-transparent my-3 text-white" />


                <label htmlFor="age" className="fs-5 fw-light" >Age:</label>
                <input onChange={getUser} type="number" id="age" name="age" className="form-control bg-transparent my-3 text-white" />

                

                {errorList.map((error,index)=> <div className="text-danger" key={index}>{error.message}</div>)}

                {errors?<div className="text-danger">{errors}</div>:''}
                
                <button className="btn btn-outline-info my-4" type="submit">{loadingSpiner?<i className="fa-solid fa-spinner"></i> :'Register'}</button>

            </form>
        </div>
        </div>
    )
}

export default Register;