import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { MediaContext } from "../../Components/MediaContext/MediaContext";



function Home(){

    

    let baseImgurl ='https://image.tmdb.org/t/p/original/';
    let{ trendingMovies} = useContext(MediaContext);
    let{ trendingTv} = useContext(MediaContext);

  
    return(
        <div className=" home py-5">
        <div className=" row py-5 gy-5">
            
            <div className="col-md-4 d-flex align-items-center">
                <div className="w-100"> 
                <div className="line w-25"></div>
                    <h2 className="mt-3">Trending<br/> movies<br/> to watcch now</h2>
                    <p className="secondary-Font-color">most watched movies by days</p>
                <div className="line mt-3"></div>
                </div>
            </div>
            

            {trendingMovies.map((movie,index)=>
            <div className="col-md-2" key={index}>
                <Link to={`/details/${movie.id}/${movie.media_type}`}>
                <img className="w-100" src={baseImgurl+movie.poster_path} alt="" />
                <h5 className="mt-3">{movie.title}</h5>
                </Link>
            </div>)
                }


        </div>

        <div className=" row  gy-5">
            
            <div className="col-md-4 d-flex align-items-center">
                <div className="w-100"> 
                <div className="line w-25"></div>
                    <h2 className="mt-3">Trending<br/> TV<br/> to watch now</h2>
                    <p className="secondary-Font-color">most watched tv Shows by days</p>
                <div className="line mt-3"></div>
                </div>
            </div>
            

            {trendingTv.map((tv,index)=>
            <div className="col-md-2" key={index}>
                <Link to={`/details/${tv.id}/${tv.media_type}`}>
                <div>
                <img className="w-100" src={baseImgurl+tv.poster_path} alt="" />
                <h5 className="mt-3">{tv.name}</h5>
                </div>
                </Link> 
               
            </div>)
                }


        </div>
        </div>


    )
}

export default Home;